@import "//db.onlinewebfonts.com/c/0fac23294cabd9471f8ca7816bf12eae?family=FuturaPTW02-Book";
:root {
  --spacing-zero: 0px;
  --spacing-xs: 4px;
  --spacing-s: 8px;
  --spacing-m: 12px;
  --spacing-l: 16px;
  --spacing-xl: 24px;
  --spacing-xxl: 32px;
  --spacing-xxxl: 48px;
  --slide-height: 96vh;
  --margin-top: 2rem;
  --modal-height: 96vh;
  --box-shadow-primary: 0 1rem 2rem #00000026;
  --box-shadow-secondary: 0 .1rem .8rem #0000002e;
  --width-box-A: calc(100% - var(--width-box-B));
  --width-box-B: 33.3333%;
}

:root, :root[data-theme="light"] {
  --color-grey-light-1: #faf9f9;
  --color-grey-light-2: #f4f2f2;
  --color-grey-light-3: #f0eeee;
  --color-grey-light-4: #ccc;
  --color-grey-dark-1: #333;
  --color-grey-dark-2: #777;
  --color-grey-dark-3: #999;
  --shadow-dark: 0 2rem 6rem #0000004d;
  --shadow-light: 0 2rem 5rem #0000000f;
  --line: 1px solid var(--color-grey-light-2);
  --background-color-primary: #f7f7f7;
  --background-color-secondary: #fffffff2;
  --background-color-tertiary: #f2f2f2;
  --bg-primary: #fff;
  --bg-primary--shade--one: #f5f5f5;
  --bg-primary--shade--two: #ebebeb;
  --bg-primary--shade--three: #d9d9d9;
  --bg-primary--shade--four: #bfbfbf;
  --text-color: #6b6b6b;
  --text-content-color-secondary: gray;
  --border-primary: 1px solid #00000020;
  --border-secondary: 1px solid #ededed;
  --color-darkest: #000;
  --color-darkest--shade--one: #323232;
  --color-darkest--shade--two: #3c3c3c;
  --color-darkest--shade--three: #4e4e4e;
  --color-darkest--shade--four: #686868;
  --hairline-regular: #eaeaea;
  --hairline-regular--shade--one: #e0e0e0;
  --hairline-regular--shade--two: #d6d6d6;
  --hairline-regular--shade--three: #c4c4c4;
  --hairline-regular--shade--four: #aaa;
  --hairline-strong: #d4d4d4;
  --hairline-strong--shade--one: #cacaca;
  --hairline-strong--shade--two: silver;
  --hairline-strong--shade--three: #aeaeae;
  --hairline-strong--shade--four: #949494;
  --reverse-bg-primary: #000c;
  --scrollbar-thumb-background-color: #888;
}

:root[data-theme="dark"] {
  --background-color-primary: #282828;
  --background-color-secondary: #2b2b2bf2;
  --background-color-tertiary: #323232;
  --bg-primary: #303030;
  --bg-primary--shade--one: #3a3a3a;
  --bg-primary--shade--two: #444;
  --bg-primary--shade--three: #565656;
  --bg-primary--shade--four: #707070;
  --text-color: #fff;
  --text-content-color-secondary: #a6a6a6;
  --border-primary: 1px solid #00000020;
  --border-secondary: 1px solid #303030;
  --reverse-bg-primary: white;
  --hairline-regular: #eaeaea;
  --hairline-regular--shade--one: #e0e0e0;
  --hairline-regular--shade--two: #d6d6d6;
  --hairline-regular--shade--three: #a6a6a6;
  --hairline-regular--shade--four: #aaa;
  --hairline-strong: #d4d4d4;
  --hairline-strong--shade--one: #cacaca;
  --hairline-strong--shade--two: silver;
  --hairline-strong--shade--three: #aeaeae;
  --hairline-strong--shade--four: #949494;
}

:root[data-theme="dark"] img {
  opacity: .82;
}

*, :before, :after {
  -webkit-box-sizing: inherit;
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

html {
  height: 100%;
  box-sizing: border-box;
  font-size: 62.5%;
}

body {
  background-color: var(--background-color-primary);
  color: var(--text-color);
  min-height: 100%;
  box-sizing: border-box;
  flex-direction: column;
  font-family: FuturaPTW02-Book, Helvetica, Arial, sans-serif;
  font-weight: 300;
  line-height: 1.6rem;
  text-decoration: none;
  display: flex;
}

.main {
  background-color: var(--background-color-primary);
  overflow-x: clip;
  width: 100% !important;
}

@media (max-width: 37.5em) {
  .main {
    padding: 0 0;
  }
}

#section-gallery {
  position: relative;
}

.section-gallery {
  width: calc(68.169% - 192px);
  margin: 0 96px;
  display: block;
}

@media (max-width: 37.5em) {
  .section-gallery {
    width: 100%;
  }
}

.nav .site-title {
  font-size: 1.9rem;
  font-weight: normal;
}

.nav__link {
  color: var(--text-content-color-secondary);
  letter-spacing: 0;
  font-size: 1.7rem;
  text-decoration: none;
}

.nav__link:hover {
  text-decoration: none;
}

.btn {
  font-size: 1.4rem;
}

.aside__info-container dl {
  font-size: 1.5rem;
  text-decoration: none;
}

.aside dd {
  text-transform: capitalize;
  text-decoration: none;
}

.aside__description {
  color: var(--text-content-color-secondary);
  text-align: justify;
  text-justify: inter-word;
  font-size: 1.45rem;
  font-weight: 100;
}

.aside__tags {
  text-align: justify;
  text-justify: inter-word;
  font-size: 1.3rem;
}

.card__details ul li {
  text-transform: capitalize;
  text-align: start;
  color: var(--text-content-color-secondary);
  font-size: 1.3rem;
  line-height: 1.2rem;
}

.card__details ul li:not(:first-child) {
  color: var(--text-content-color-secondary);
}

.card__details ul li.card__artwork-title {
  text-align: start;
  text-overflow: ellipsis;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  display: -webkit-box;
  overflow: hidden;
}

.card__details ul li.card__artwork-title:hover {
  overflow: visible;
}

.card__details ul li.card__artwork-date, .card__details ul li.card__artwork-dimensions {
  font-size: 1.1rem;
}

.cp__btn {
  color: var(--text-content-color-secondary);
}

.filter__h3 {
  color: var(--text-content-color-secondary);
  text-transform: capitalize;
  font-size: 1.3rem;
  font-weight: 600;
}

.filter-box {
  font-size: 1.45rem;
}

.filter-box__heading {
  font-family: inherit;
  font-size: 1.3rem;
}

.filter-box__btn {
  text-transform: lowercase;
  color: var(--text-content-color-secondary);
  font-family: inherit;
  font-size: 1.3rem;
}

.search__input::-webkit-input-placeholder {
  color: var(--text-content-color-secondary);
  font-weight: 100;
}

.filter-menu__clear-btn {
  color: var(--text-content-color-secondary);
}

.hidden:not(.cog-btn) {
  visibility: hidden;
  bottom: 0;
}

.full-width {
  margin: 0 auto;
  width: 100% !important;
  max-width: calc(68.169% - 192px) !important;
}

.collapse-Y {
  transform: translateY(100%);
}

.collapse-X {
  transform: translateX(100%);
}

.u-spacing-x {
  background-color: var(--color-grey-dark-1);
  width: 1px;
}

.u-spacing-s {
  height: var(--spacing-xs);
  height: var(--spacing-s);
}

.u-spacing-m {
  height: var(--spacing-m);
}

.u-spacing-l {
  height: var(--spacing-l);
}

.u-spacing-xl {
  height: var(--spacing-xl);
}

.u-spacing-xxl {
  height: var(--spacing-xxl);
}

.u-spacing-xxxl {
  height: var(--spacing-xxxl);
}

.u-spacing-navbar {
  height: calc(6rem + 18px);
}

.alert {
  z-index: 9999;
  color: #fff;
  text-align: center;
  padding: 1.6rem 15rem;
  font-size: 1.6rem;
  font-weight: 400;
  position: fixed;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

.alert--success {
  background-color: #20bf6b;
}

.alert--error {
  background-color: #eb4d4b;
}

.btn {
  cursor: pointer;
}

.card__btn--bookmark {
  height: 3rem;
  width: 3rem;
  z-index: 200;
  background-color: #0000;
  border: none;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  bottom: 2rem;
  right: 2rem;
}

.card__btn--bookmark svg {
  cursor: pointer;
  height: auto;
  width: 2.25rem;
  visibility: hidden;
  opacity: 0;
  z-index: 201;
  transition: visibility, opacity .5s linear;
}

.card__image-container:hover svg {
  visibility: visible;
  opacity: 1;
  transition: all .2s;
}

.card__icon-bookmark {
  height: 1rem;
  width: auto;
}

.btn--toggle__box-A, .btn--toggle__box-B {
  width: .2rem;
  justify-content: center;
  align-items: center;
  display: flex;
}

@media (max-width: 37.5em) {
  .btn--toggle__box-A, .btn--toggle__box-B {
    display: none;
  }
}

.aside__icon-bookmark {
  fill: var(--background-color-primary);
  height: 1rem;
  width: auto;
}

.aside__icons-container {
  flex-direction: row;
  justify-content: flex-end;
  display: flex;
}

.aside__btn--zoom-in, .aside__btn--zoom-out {
  background-color: var(--bg-primary--shade--four);
  height: 2.1rem;
  width: 2.1rem;
  cursor: pointer;
  border: none;
  font-size: 2rem;
  position: absolute;
}

.aside__btn--zoom-in:hover, .aside__btn--zoom-out:hover {
  opacity: .8;
}

.aside__btn--zoom-in svg, .aside__btn--zoom-out svg {
  cursor: pointer;
  height: auto;
  width: 2.25rem;
  visibility: hidden;
  opacity: 0;
  transition: visibility, opacity .5s linear;
}

.aside__btn--zoom-in {
  border-radius: 5px 5px 0 0;
  top: 2.5rem;
  right: 2.1rem;
}

.aside__btn--zoom-out {
  border-radius: 0 0 5px 5px;
  top: 4.6rem;
  right: 2.1rem;
}

.aside__btn--bookmark {
  height: 3rem;
  width: 3rem;
  background-color: #0000;
  border: none;
  justify-content: center;
  align-items: center;
  display: flex;
  bottom: 2rem;
  right: 2rem;
}

.aside__btn--bookmark svg {
  cursor: pointer;
  height: auto;
  width: 2.25rem;
  transition: visibility, opacity .5s linear;
}

.aside__btn--close {
  height: 3rem;
  width: 3rem;
  border: none;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 10rem;
  right: 10rem;
}

.aside__btn--close svg {
  cursor: pointer;
  height: auto;
  width: 2.25rem;
}

#control-panel {
  z-index: 1000;
}

.control-panel {
  background-color: var(--background-color-primary);
  max-height: -moz-fit-content;
  max-height: fit-content;
  position: fixed;
  bottom: 0;
}

.control-panel__nav-bar {
  width: 100%;
  background-color: var(--background-color-primary);
  flex-direction: row;
  display: flex;
}

.control-panel .hidden:not(.cog-btn, .cog-icon) {
  visibility: hidden;
  height: .5rem;
  bottom: 0;
}

.cp__btn {
  background-color: var(--background-color-tertiary);
  border: none;
  padding: 3px 12px;
}

.cp__btn:not(:last-child) {
  border-right: var(--border-primary);
}

.cp__btn:hover {
  background-color: var(--background-color-primary);
}

.cog-btn {
  z-index: 300;
  visibility: visible !important;
}

.control-panel__btn-bar {
  width: 100%;
  background-color: var(--background-color-tertiary);
  flex-direction: row;
  justify-content: space-between;
  display: flex;
}

.gallery-control-panel {
  height: 12rem;
  overflow-y: auto;
}

.gallery-settings, .gallery-switch-wrapper {
  flex-direction: row;
  justify-content: space-between;
  display: flex;
}

.gall__btn {
  cursor: pointer;
}

.btn--toggle__control-panel {
  width: 2.5rem;
  justify-content: center;
  align-items: center;
  display: flex;
}

.btn--toggle__control-panel svg {
  height: 2rem;
  width: 2rem;
}

.cp__btn--active {
  background-color: var(--bg-primary--shade--two);
}

.filter-menu {
  background-color: var(--background-color-primary);
  grid-area: sidebar;
  display: flex;
  position: sticky;
  bottom: 0;
  overflow-y: auto;
}

.filter-menu__checkbox:checked ~ .filter-menu__filter-navbar {
  opacity: 1;
  height: max-content;
}

.filter-menu__control-btns-bar {
  background-color: var(--background-color-secondary);
  flex-direction: row;
  justify-content: flex-end;
  display: flex;
}

.filter-menu .filter-box {
  max-height: 32rem;
  background-color: var(--background-color-tertiary);
  border: var(--border-primary);
  border-radius: 5px;
  flex-flow: column wrap;
  margin: 1rem .5rem;
  display: flex;
}

.filter-menu .filter-box .filter__btns-wrapper {
  padding: var(--spacing-xs);
  border-top: 1px solid var(--hairline-strong--shade--four);
  border-radius: 0;
}

.filter-menu .filter-box__heading {
  grid-area: heading;
  padding-bottom: .5rem;
}

.filter-menu .filter-box__btn {
  cursor: pointer;
  background-color: var(--background-color-secondary);
  border: var(--border-primary);
  border-radius: 5px;
  margin: .25rem;
  padding: .35rem;
}

.filter-menu .filter-box__btn:hover {
  -webkit-box-shadow: var(--box-shadow-tertiary);
  box-shadow: var(--box-shadow-tertiary);
  transform: translateY(-1px);
}

.filter-menu .filter-box__btn--active {
  -webkit-box-shadow: var(--box-shadow-tertiary);
  box-shadow: var(--box-shadow-tertiary);
  background-color: #39f3;
  border: 1px solid #87cefa41;
  transform: translateY(-1px);
}

.filter-menu .filter-box--tags {
  grid-area: tags;
}

.filter-menu .filter-box--date {
  grid-area: date;
}

.filter-menu .filter-box--location {
  grid-area: location;
}

.filter-menu .filter-box--price {
  grid-area: price;
}

.filter-menu .filter-box--medium {
  grid-area: med;
}

.pagination {
  flex-direction: row;
  flex: 0 0 1px;
  display: flex;
}

.btn--inline {
  flex-direction: row;
  display: flex;
}

.btn--toggle__box-A, .btn--toggle__box-B {
  width: max-content;
  display: flex;
}

.search-group {
  justify-content: center;
  align-items: center;
  gap: var(--spacing-xxxl);
  flex-direction: row;
  display: flex;
}

.search {
  flex: 0 0 60%;
  justify-content: center;
  align-items: center;
  display: flex;
}

@media only screen and (max-width: 31.25em) {
  .search {
    background-color: var(--background-color-secondary);
    flex: 0 0 100%;
    order: 1;
  }
}

.search__input {
  background-color: var(--background-color-secondary);
  border: 1px solid var(--border-primary);
  min-width: 22rem;
  max-width: 25rem;
  border-radius: 100px;
  margin-right: -3.25rem;
  padding: .7rem 1.5rem;
  transition: all .2s;
}

.search__input:focus {
  width: 100%;
  background-color: var(--background-color-tertiary);
  outline: none;
}

.search__input:focus + .search__button {
  background-color: var(--background-color-tertiary);
}

.search__button {
  cursor: pointer;
  background-color: var(--background-color-secondary);
  border: none;
  display: flex;
}

.search__button:focus {
  outline: none;
}

.search__button:active {
  transform: translateY(2px);
}

.search__icon {
  height: 1.5rem;
  width: 1.5rem;
  fill: var(--bg-primary--shade--four);
}

.filter-menu__clear-btn {
  height: 75%;
  background-color: var(--background-color-primary);
  cursor: pointer;
  border: none;
  border-radius: 100px;
  justify-content: center;
  align-items: center;
  padding: .7rem 1.5rem;
  display: flex;
}

.theme-switch-wrapper {
  flex-direction: row;
  display: flex;
}

.cp__icon-bookmark {
  fill: var(--color-grey-light-4);
}

.cp__icon-bookmark svg {
  cursor: pointer;
  height: auto;
  width: 2.25rem;
}

.cp__icon-bookmark, .icon-img {
  height: 2.5rem;
  width: 2.5rem;
  border: none;
  display: flex;
}

:root .icon-img, :root[data-theme="light"] .icon-img {
  filter: invert(50%);
}

:root[data-theme="dark"] .icon-img {
  filter: invert(80%);
}

.filter__h3-wrap {
  padding: .1rem 0 .1rem var(--spacing-s);
}

.filter-box__container {
  padding: 1.3rem;
}

.filter-box__btn {
  cursor: pointer;
}

.filter-box__btn .active {
  background-color: #87cefa;
  border: 1px solid #87cefa;
}

.login-form__container {
  display: flex;
}

.login-form {
  max-width: 50rem;
  background-color: #fff;
  border-radius: 5px;
  margin: 25vh auto;
  padding: 5rem 7rem;
  box-shadow: 0 2.5rem 8rem 2rem #0000000f;
}

.form__input {
  color: inherit;
  width: 100%;
  box-sizing: border-box;
  background-color: #f2f2f2;
  border: 3px solid #0000;
  border-left: none;
  border-right: none;
  border-radius: 4px;
  padding: 1.25rem 1.75rem;
  font-family: inherit;
  font-size: 1.5rem;
  transition: all .3s;
  display: block;
}

.form__input:focus {
  border-bottom: 3px solid #55c57a;
  outline: none;
}

.form__input:focus:invalid {
  border-bottom: 3px solid #ff7730;
}

.form__input::-webkit-input-placeholder {
  color: #bbb;
}

.form__group:not(:last-child) {
  margin-bottom: 2.5rem;
}

.form__label {
  margin-bottom: .75rem;
  font-size: 1.6rem;
  font-weight: 700;
  display: block;
}

.form__photo-upload {
  align-items: center;
  font-size: 1.6rem;
  display: flex;
}

.form__user-photo {
  height: 7.5rem;
  width: 7.5rem;
  border-radius: 50%;
  margin-right: 2rem;
}

.form__upload {
  width: .1px;
  height: .1px;
  opacity: 0;
  z-index: -1;
  position: absolute;
  overflow: hidden;
}

.form__upload:focus + label {
  outline-offset: 3px;
  outline: 3px solid #55c57a;
}

.form__upload + label {
  color: #55c57a;
  cursor: pointer;
  border-bottom: 1px solid #55c57a;
  padding: 3px;
  text-decoration: none;
  transition: all .2s;
  display: inline-block;
}

.form__upload + label:hover {
  color: #fff;
  background-color: #55c57a;
  transform: translateY(-2px);
  box-shadow: 0 1rem 2rem #00000026;
}

.modal__title {
  color: var(--text-color);
  text-align: center;
  padding: 15px 0;
  font-size: 1.3rem;
  text-decoration: none;
}

.popover__wrapper {
  margin-top: 1.5rem;
  display: inline-block;
  position: relative;
}

.popover__content {
  opacity: 0;
  visibility: hidden;
  width: auto;
  background-color: #bfbfbf;
  padding: 1.5rem;
  position: absolute;
  transform: translate(0, 10px);
  box-shadow: 0 2px 5px #00000042;
}

.popover__content:before {
  z-index: -1;
  content: "";
  border: 10px solid #0000;
  border-top-width: 0;
  border-bottom-color: #bfbfbf;
  transition-property: transform;
  transition-duration: .3s;
  position: absolute;
  top: -8px;
  right: calc(50% - 10px);
}

.popover__wrapper:hover .popover__content {
  z-index: 10;
  opacity: 1;
  visibility: visible;
  transition: all .5s cubic-bezier(.75, -.02, .2, .97);
  transform: translate(0, -20px);
}

.popover__message {
  text-align: center;
}

.nav {
  background-color: var(--bg-primary--shade--one);
  color: var(--text-color);
  height: 6rem;
  width: 100%;
  z-index: 10000;
  border-bottom: 1px solid var(--hairline-strong--shade--four);
  justify-content: space-between;
  align-items: center;
  padding: 0 2rem;
  display: flex;
  position: fixed;
}

.nav:hover {
  background-color: var(--background-color-tertiary);
}

.nav:first-child, .nav:last-child {
  padding: 0 3rem;
}

.nav__link:hover {
  background-color: var(--bg-primary--shade--three);
  cursor: pointer;
}

.nav__links__container {
  flex-direction: row;
  gap: 5rem;
  display: flex;
}

.nav__list {
  list-style-type: none;
  display: flex;
}

.nav__btn {
  height: 2.5rem;
  width: 2.5rem;
  justify-content: center;
  align-items: center;
  display: flex;
}

.nav__btn svg {
  height: 2rem;
  width: 2rem;
}

.nav__link {
  height: 100%;
  cursor: pointer;
  padding: 0 1.6rem;
}

.nav__btn {
  background-color: var(--background-color-secondary);
  border: none;
}

html ::-webkit-scrollbar {
  height: 12px;
  width: 12px;
  overflow: visible;
}

html ::-webkit-scrollbar-button {
  height: 0;
  width: 0;
}

html ::-webkit-scrollbar-corner {
  background: none;
}

html ::-webkit-scrollbar-thumb {
  background-color: var(--scrollbar-thumb-background-color);
}

